@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    src: url('../Fonts/Lato/lato-v15-latin-700.eot');
    src: url('../Fonts/Lato/lato-v15-latin-regular.eot');
}

.friendsWrapper {
    margin: auto;
    margin: 100px 0 0;
}

.item {
    margin: 15px 5px;
    width: 400px;
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: left;
    flex-wrap: wrap;
    /* border: 1px solid red; */
    -webkit-box-shadow: 0px 6px 10px 1px rgba(168, 168, 168, 1);
    -moz-box-shadow: 0px 6px 10px 1px rgba(168, 168, 168, 1);
    box-shadow: 0px 6px 10px 1px rgba(168, 168, 168, 1);
}

.status {
    margin: 0px 20px;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 10%;
}

.name {
    margin: 0px 20px;
    font-size: 28px;
    font-family: 'Lato';
}